.circle-preloader {
  display: block;
  width: 50px;
  height: 50px;
  border: 4px solid #444;
  border-bottom-color: #888;

  border-radius: 50%;

  margin: 0 auto;

  -webkit-animation: spin 0.75s infinite linear;

          animation: spin 0.75s infinite linear;
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.card {
  border: 3px solid #bbb;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 200px;
  box-shadow: rgba(255, 255, 255, 0.15) 2.4px 2.4px 3.2px;
  margin-bottom: 10px;
}

.card__img {
  height: 200px;
  width: 200px;
}

.card__img-background {
  width: inherit;
  height: inherit;
  object-fit: cover;
}

.card__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
}

.card__content-title {
  font-family: 'Inter', sans-serif;
  font-weight: bold;
}

.card__content-link {
  color: coral;
  margin: auto;
}

.card__content-edit {
  font-family: 'Inter', sans-serif;
  font-style: italic;
}

.card__content-link:visited {
  color: blueviolet;
}

@media screen and (min-width: 425px) and (max-width: 768px) {
  .card {
    flex-direction: column;
    height: auto;
  }

  .card__img {
    width: 100%;
  }
}

@media screen and (max-width: 425px) {
  .card {
    flex-direction: column;
    height: auto;
  }
  .card__img {
    width: 100%;
  }
  .card__content {
    font-size: 10px;
  }
}

.main {
  margin: 20px 200px auto 200px;
}

.main__title {
  font-family: 'Inter', sans-serif;
  font-size: 1.5rem;
}

.main__forms {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.main__content {
  margin: 20px 0;
}

.main__forms-item {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  align-items: center;
}

.main__forms-item-input {
  border: 1px solid var(--select-border);
  border-radius: 0.25em;
  padding: 0.25em 0.5em;

  font-family: 'Inter', sans-serif;
  font-size: 0.9rem;
  cursor: pointer;
  line-height: 1.1;

  background-color: #fff;
  background-image: linear-gradient(to top, #f9f9f9, #fff 33%);
}

.main__forms-item-button {
  background-color: #ffffff;
  border: 1px solid rgb(209, 213, 219);
  border-radius: 0.5rem;
  box-sizing: border-box;
  color: #111827;
  font-size: 0.875rem;
  font-family: 'Inter', sans-serif;
  font-weight: 600;
  line-height: 0.5rem;
  padding: 0.5rem 1rem;
  text-align: center;
  -webkit-text-decoration: none #d1d5db solid;
          text-decoration: none #d1d5db solid;
  text-decoration-thickness: auto;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  cursor: pointer;
  height: 28px;
}

.main__forms-item-button:hover:enabled {
  background-color: rgb(249, 250, 251);
}

.main__forms-item-button:disabled {
  opacity: 0.3;
  pointer-events: none;
}

@media screen and (min-width: 1024px) and (max-width: 1280px) {
  .main__forms-item {
    flex-direction: column;
  }
}

@media screen and (max-width: 1024px) {
  .main__forms-item {
    flex-direction: column;
  }
  .main__forms {
    flex-direction: column;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
  }

  .main__forms-item-input {
    width: 50vw;
  }

  .main {
    margin: auto 50px;
  }

  .main__title {
    font-size: 1rem;
  }
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.header__img {
  fill: white;
  height: 5vh;
}
.header__title {
  font-family: 'Inter', sans-serif;
  font-size: 2rem;
}

@media screen and (min-width: 740px) and (max-width: 1024px) {
  .header__title {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 740px) {
  .header__title {
    font-size: 1.2rem;
  }
}

.navigation {
  display: flex;
  margin: 0 200px;
  border-bottom: 1px solid #ccc;
  font-family: 'Inter', sans-serif;
}

.navigation__menu {
  display: flex;
  align-self: center;
  margin-left: auto;
}

.navigation__menu-item {
  margin-left: 50px;
  text-decoration: none;
  color: #fff;
}

.navigation__menu-item:hover {
  color: #aaa;
}

@media screen and (min-width: 740px) and (max-width: 1024px) {
  .navigation {
    margin: 0 50px;
  }
}

@media screen and (max-width: 740px) {
  .navigation {
    flex-direction: column;
    margin: 0 50px;
  }

  .navigation__menu {
    display: flex;
    align-self: center;
    margin: 0 auto;
    grid-gap: 50px;
    gap: 50px;
  }

  .navigation__menu-item {
    margin-left: 0;
  }
}

.footer__text {
  font-family: 'Inter', sans-serif;
}

@media screen and (max-width: 425px) {
  .footer__text {
    font-size: 0.8rem;
  }
}

.about {
  margin: 20px 200px auto 200px;
}

.about__text {
  font-family: 'Inter', sans-serif;
  text-align: justify;
  font-size: 1rem;
}

@media screen and (max-width: 1024px) {
  .about {
    margin: 20px 50px auto 50px;
  }
}

